import { IDataType } from '../i-data-type';
import { DataType } from '../data-type';
import { DataTypeConst } from '../../constant/data-type-const';

export class ObjectType extends DataType implements IDataType{

    constructor(){
        super();
        this.kind=DataTypeConst.DEFAULT_OBJECT;
    }

    getKind(): string {
       return this.kind;
    }    

}