import { IOperationSerializerFactory } from '../../extension/factory/i-operation-serializer-factory';
import { IOperationSerializer } from '../../extension/serializer/i-operation-serializer';
import { DEFAULT_OPERATION } from '../default-constant';
import { DefaultOperationSerializer } from '../serializer/default-operation-serializer';

export class DefaultOperationSerializerFactory implements IOperationSerializerFactory {
    private serializerDict: { [key: string]: IOperationSerializer } = {};

    get(kind: string): IOperationSerializer {
        switch (kind) {
            case DEFAULT_OPERATION:
                if (!this.serializerDict[kind]) {
                    var serializer = new DefaultOperationSerializer();
                    this.serializerDict[kind] = serializer;
                }
                break;
            default:
                throw new Error(`${kind}!`);
        }
        return this.serializerDict[kind];
    }

}