import { CommonStructureConvertor } from './common-structure-convertor';
import { ValueObject } from '../../define/structure/value-object';
import { ISerializeContext } from '../../serialize-context/extension/i-serialize-context';
import { StructuredType } from '../../define/data-type/structured-type/structured-type';
import { ReferReplaceUtil } from '../utils/refer-replace-util';
import { ICommonStructureSerializeContext } from '../../serialize-context/extension/i-common-structure-serialize-context';

export class ValueObjectConvertor {
     convertToObject(obj:Object,vo: ValueObject,ctxt:ICommonStructureSerializeContext): void {
        let structureConvertor=new CommonStructureConvertor();  
        structureConvertor.convertToObject(obj,vo,ctxt);
        //first?
        var type=vo.dataType;
        obj['structureId']=type.id;
    }

     initFromObject(vo:ValueObject,obj: Object,ctxt:ICommonStructureSerializeContext): void {
        let structureConvertor=new CommonStructureConvertor();  
        structureConvertor.initFromObject(vo,obj,ctxt);
        //root
        let allStructureTypes:Array<StructuredType>=ReferReplaceUtil.getAllStructuredTypes(vo);
        ReferReplaceUtil.replaceOperationRefer(allStructureTypes,vo.operations);
    }
}