import { IParameterSerializer } from '../../extension/serializer/i-parameter-serializer';
import { Parameter } from '../../../define/parameter/parameter';
import { ISerializeContext } from '../../extension/i-serialize-context';
import { ParameterConvertor } from '../../../convertor/parameter/parameter-convertor';

export class DefaultParameterSerializer implements IParameterSerializer{
    serialize(param: Parameter, ctxt: ISerializeContext): Object {
        let result=new Object();
        let baseConvertor=new ParameterConvertor();
        baseConvertor.convertToObject(result,param,ctxt);
        return result;
    }    

    deserialize(obj: Object, ctxt: ISerializeContext): Parameter {
        let result=new Parameter();
        let baseConvertor=new ParameterConvertor();
        baseConvertor.initFromObject(result,obj,ctxt);
        return result;

    }


}