import { IOperationSerializer } from '../../extension/serializer/i-operation-serializer';
import { IConstraintSerializerFactory } from '../../extension/factory/i-constraint-serializer-factory';
import { IConstraint } from '../../../define/constraint/i-constraint';
import { IConstraintSerializer } from '../../extension/serializer/i-constraint-serializer';
import { DEFAULT_CONSTRAINT } from '../default-constant';
import { DefaultConstraintSerializer } from '../serializer/default-constraint-serializer';

export class DefaultConstraintSerializerFactory implements IConstraintSerializerFactory {

    private serializerDict: { [key: string]: IConstraintSerializer } = {};

    get(kind: string): IConstraintSerializer {
        switch (kind) {
            case DEFAULT_CONSTRAINT:
                if (!this.serializerDict[kind]) {
                    var serializer = new DefaultConstraintSerializer();
                    this.serializerDict[kind] = serializer;
                }
                break;
            default:
                throw new Error(`${kind}!`);
        }
        return this.serializerDict[kind];
    }
    
}