import { IDataType } from '../../define/data-type/i-data-type';
import { StructuredTypeConvertor } from './structured-type-convertor';
import { EntityType } from '../../define/data-type/structured-type/entity-type/entity-type';
import { ISerializeContext } from '../../serialize-context/extension/i-serialize-context';
import { EnumerationType } from '../../define/data-type/structured-type/enumeration-type/enumeration-type';
import { EnumerationItemConvertor } from './enumeration-item-convertor';
import { EnumerationItem } from '../../define/data-type/structured-type/enumeration-type/enumeration-item';
import { ExtensionConvertor } from '../extension/extension-convertor';

export class EnumerationTypeConvertor {

    convertToObject(obj: Object, datatype: IDataType, ctxt: ISerializeContext): void {
        let structuredConvertor: StructuredTypeConvertor = new StructuredTypeConvertor();
        structuredConvertor.convertToObject(obj, datatype, ctxt);
        let enumerationType = datatype as EnumerationType;
        //enum items
        obj['enumerationItems'] = new Array<Object>();
        if (enumerationType.enumerationItems) {
            let convertor: EnumerationItemConvertor = new EnumerationItemConvertor();
            enumerationType.enumerationItems.forEach(item => {
                let itemObj = new Object();
                convertor.convertToObject(itemObj, item);
                obj['enumerationItems'].push(itemObj);
            });
        }


        if (enumerationType.extension) {
            let extensionConvertor = new ExtensionConvertor();
            extensionConvertor.addToJObj(enumerationType, obj);
        }

    }

    initFromObject(dataType: IDataType, obj: Object, ctxt: ISerializeContext): void {
        let structuredConvertor: StructuredTypeConvertor = new StructuredTypeConvertor();
        structuredConvertor.initFromObject(dataType, obj, ctxt);
        //enum items
        let enumerationType = dataType as EnumerationType;
        if (obj['enumerationItems']) {
            enumerationType.enumerationItems = new Array<EnumerationItem>();
            let convertor: EnumerationItemConvertor = new EnumerationItemConvertor();
            obj['enumerationItems'].forEach(itemObj => {
                let item: EnumerationItem = new EnumerationItem();
                convertor.initFromObject(item, itemObj);
                enumerationType.enumerationItems.push(item);
            });
        }

        if (obj["extension"]) {
            let extensionConvertor = new ExtensionConvertor();
            extensionConvertor.addToObj(enumerationType, obj);
        }
    }
}