import { Extension } from '../../extension';

export class ExtensionConvertor{

    addToJObj(extensionObj:Extension,jObj:Object){
        jObj['extension'] = extensionObj.extension;

    }

    addToObj(extensionObj:Extension,jObj:Object){
        if (jObj['extension']) {
            extensionObj.extension = jObj['extension'];
        }
    }
}