import { IOperationSerializer } from '../../extension/serializer/i-operation-serializer';
import { ISerializeContext } from '../../extension/i-serialize-context';
import { Operation } from '../../../define/operation/operation';
import { OperationConvertor } from '../../../convertor/operation/operation-convertor';

export class DefaultOperationSerializer implements IOperationSerializer {
    serialize(op: Operation, ctxt: ISerializeContext): Object {
        let result = new Object();
        let baseConvertor = new OperationConvertor();
        baseConvertor.convertToObject(result, op, ctxt);
        return result;
    }

    deserialize(obj: Object, ctxt: ISerializeContext): Operation {
        let result = new Operation();
        let baseConvertor = new OperationConvertor();
        baseConvertor.initFromObject(result, obj, ctxt);
        return result;
    }


}