import { PrimitiveType } from '../../define/data-type/primitive-type/primitive-type';
import { PrimitiveTypeKind } from '../../define/data-type/primitive-type/primitive-type-kind';
import { IDataType } from '../../define/data-type/i-data-type';
import { ISerializeContext } from '../../serialize-context/extension/i-serialize-context';
import { ExtensionConvertor } from '../extension/extension-convertor';

export class PrimitiveTypeConvertor {

    convertToObject(obj: Object, datatype: IDataType, ctxt: ISerializeContext): void {
        if (!datatype) {
            return;
        }
        let primitivetype = datatype as PrimitiveType;
        obj['kind'] = primitivetype.kind;
        //number
        let primitiveTypeKindNum = primitivetype.primitiveTypeKind;
        obj['primitiveTypeKind'] = primitiveTypeKindNum;

        if (primitivetype.extension) {
            let extensionConvertor = new ExtensionConvertor();
            extensionConvertor.addToJObj(primitivetype, obj);
        }
        
    }

    initFromObject(dataType: IDataType, obj: Object, ctxt: ISerializeContext): void {
        let primitivetype = dataType as PrimitiveType;
        if (!obj) {
            return;
        }
        primitivetype.kind = obj['kind'];
        primitivetype.primitiveTypeKind = obj['primitiveTypeKind'] as number;
        if (obj["extension"]) {
            let extensionConvertor = new ExtensionConvertor();
            extensionConvertor.addToObj(primitivetype, obj);
        }
    }
}