import { IExtension } from './i-extension';

export class Extension implements IExtension {

    extension: { [key: string]: string };

    getKeys(): any[] {
        let result = new Array<string>();
        for (let key in this.extension) {
            result.push(key);
        }
        return result;
    }

    getValue(key: any) {
        this.extension[key];
    }
}