import { EnumerationItem } from '../../define/data-type/structured-type/enumeration-type/enumeration-item';

export class EnumerationItemConvertor{
    convertToObject(obj:Object,item: EnumerationItem): void {
        obj['code'] = item.code;
        obj['value'] = item.value;
        obj['description'] = item.description;
    }

    initFromObject(item:EnumerationItem,obj: Object): void {
        let enumItem = item as EnumerationItem;
        if (!obj) {
            return;
        }
        enumItem.code = obj['code'];
        enumItem.value = obj['value'];
        enumItem.description = obj['description'];
    }
}