import { Injectable, Inject } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { EntityObject } from '../../define/structure/entity-object';
import { DefaultSerializeContext } from '../../serialize-context/default/default-serialize-context';


@Injectable()
export class CommonStructureRTService {

    private commom_structure_url = '/api/runtime/sys/v1.0/common-structure';

    constructor(private http: HttpClient) { }

    /**
     * CommonStructure
     * @param type 
     * @param id Id
     * @param headers headersessionId
     */
    get(id: string, headers: HttpHeaders): Observable<EntityObject> {
        let idParam = encodeURIComponent(id);
        let url =  this.commom_structure_url + '?id=' + id ;
        return this.http.get<EntityObject>(url, { headers: headers }).pipe(map(
            obj => {
            //serializeContext
            let serializeCtxt = new DefaultSerializeContext();
            let kind = obj['kind'];
            let serializer = serializeCtxt.getStructureSerializerFactory().get(kind);
            return serializer.deserialize(obj, serializeCtxt) as EntityObject;
        },
        error=>{
            alert("");
            return null;
        })
        );
    };

    
}