import { IConstraint } from '../constraint/i-constraint';
import { IDataType } from '../data-type/i-data-type';
import { ElementConstant } from '../constant/element-const';
import { Extension } from '../../extension';

export class Property extends Extension{
    id:string;
    code: string;
    name: string;
    description: string;
    required: boolean;
    defaultValue: any;
    propertyType: IDataType;
    kind:string;
    constraint: IConstraint;

    constructor(){
        super();
        this.kind=ElementConstant.DEFAULT_PROPERTY;
    }
}