import { IDataTypeSerializer } from '../../extension/serializer/i-data-type-serializer';
import { IDataType } from '../../../define/data-type/i-data-type';
import { ISerializeContext } from '../../extension/i-serialize-context';
import { DataTransferObjectTypeConvertor } from '../../../convertor/data-type/data-transfer-object-type-convertor';
import { DataTransferObjectType } from '../../../define/data-type/structured-type/data-transfer-object-type/data-transfer-object-type';

export class DefaultDataTransferObjectTypeSerializer implements IDataTypeSerializer{
    serialize(type: IDataType, ctxt: ISerializeContext): Object {
        let result=new Object();
        let baseConvertor=new DataTransferObjectTypeConvertor();
        baseConvertor.convertToObject(result,type,ctxt);
        return result;
    }    
    
    deserialize(obj: Object, ctxt: ISerializeContext): IDataType {
        let result=new DataTransferObjectType();
        let baseConvertor=new DataTransferObjectTypeConvertor();
        baseConvertor.initFromObject(result,obj,ctxt);
        return result;
    }


}