import { IConstraint } from '../constraint/i-constraint';
import { IDataType } from '../data-type/i-data-type';
import { ElementConstant } from '../constant/element-const';
import { Extension } from '../../extension';

export class Parameter extends Extension {
    code: string;
    name: string;
    description: string;
    required: boolean;
    defaultValue: any;
    parameterType:IDataType;
    constraint: IConstraint;
    kind:string;
    
    constructor(){
        super();
        this.kind=ElementConstant.DEFAULT_PARAMETER;
    }
}