import { ISerializeContext } from '../extension/i-serialize-context';
import { IDataTypeSerializerFactory } from '../extension/factory/i-data-type-serializer-factory';
import { IParameterSerializerFactory } from '../extension/factory/i-parameter-serializer-factory';
import { IPropertySerializerFactory } from '../extension/factory/i-property-serializer-factory';
import { ICommonStructureSerializerFactory } from '../extension/factory/i-common-structure-serializer-factory';
import { IOperationSerializerFactory } from '../extension/factory/i-operation-serializer-factory';
import { DefaultDataTypeSerializerFactory } from './factory/default-data-type-serializer-factory';
import { DefaultOperationSerializerFactory } from './factory/default-operation-serializer-factory';
import { DefaultParameterSerializerFactory } from './factory/default-parameter-serializer-factory';
import { DefaultPropertySerializerFactory } from './factory/default-property-serializer-factory';
import { DefaultStructureSerializerFactory } from './factory/default-structure-serializer-factory';
import { ICommonStructureSerializeContext } from '../extension/i-common-structure-serialize-context';
import { IConstraintSerializerFactory } from '../extension/factory/i-constraint-serializer-factory';
import { DefaultConstraintSerializerFactory } from './factory/default-constraint-serializer-factory';

export class DefaultSerializeContext implements ICommonStructureSerializeContext {
    private dataTypeFactory: IDataTypeSerializerFactory;
    private operationFactory: IOperationSerializerFactory;
    private parameterFactory: IParameterSerializerFactory;
    private propertyFactory: IPropertySerializerFactory;
    private structureFactory: ICommonStructureSerializerFactory;
    private constraintFactory: IConstraintSerializerFactory;

    getDataTypeSerializerFactory(): IDataTypeSerializerFactory {
        if (!this.dataTypeFactory) {
            this.dataTypeFactory = new DefaultDataTypeSerializerFactory();
        }
        return this.dataTypeFactory;
    }

    getOperationSerializerFactory(): IOperationSerializerFactory {
        if (!this.operationFactory) {
            this.operationFactory = new DefaultOperationSerializerFactory();
        }
        return this.operationFactory;
    }

    getParameterSerializerFactory(): IParameterSerializerFactory {
        if (!this.parameterFactory) {
            this.parameterFactory = new DefaultParameterSerializerFactory();
        }
        return this.parameterFactory;
    }

    getPropertySerializerFactory(): IPropertySerializerFactory {
        if (!this.propertyFactory) {
            this.propertyFactory = new DefaultPropertySerializerFactory();
        }
        return this.propertyFactory;
    }

    getStructureSerializerFactory(): ICommonStructureSerializerFactory {
        if (!this.structureFactory) {
            this.structureFactory = new DefaultStructureSerializerFactory();
        }
        return this.structureFactory;
    }

    getConstraintSerializerFactory(): IConstraintSerializerFactory {
        if (!this.constraintFactory) {
            this.constraintFactory = new DefaultConstraintSerializerFactory();
        }
        return this.constraintFactory;
    }

}