import { IParameterSerializerFactory } from '../../extension/factory/i-parameter-serializer-factory';
import { IParameterSerializer } from '../../extension/serializer/i-parameter-serializer';
import { DEFAULT_PARAMETER } from '../default-constant';
import { DefaultParameterSerializer } from '../serializer/default-parameter-serializer';

export class DefaultParameterSerializerFactory implements IParameterSerializerFactory{
    private serializerDict: { [key: string]: IParameterSerializer } = {};

    get(kind: string): IParameterSerializer {
        switch (kind) {
            case DEFAULT_PARAMETER:
                if (!this.serializerDict[kind]) {
                    var serializer = new DefaultParameterSerializer();
                    this.serializerDict[kind] = serializer;
                }
                break;
            default:
                throw new Error(`${kind}!`);
        }
        return this.serializerDict[kind];
    }

}