import { IDataTypeSerializer } from '../../extension/serializer/i-data-type-serializer';
import { IDataType } from '../../../define/data-type/i-data-type';
import { ISerializeContext } from '../../extension/i-serialize-context';
import { PrimitiveTypeConvertor } from '../../../convertor/data-type/primitive-type-convertor';
import { PrimitiveType } from '../../../define/data-type/primitive-type/primitive-type';
import { EnumerationTypeConvertor } from '../../../convertor/data-type/enumeration-type-convertor';
import { EnumerationType } from '../../../define/data-type/structured-type/enumeration-type/enumeration-type';

export class DefaultEnumerationTypeSerializer implements IDataTypeSerializer{
    serialize(type: IDataType, ctxt: ISerializeContext): Object {
        let result=new Object();
        let baseConvertor=new EnumerationTypeConvertor();
        baseConvertor.convertToObject(result,type,ctxt);
        return result;
    }    
    
    deserialize(obj: Object, ctxt: ISerializeContext): IDataType {
        let result = new EnumerationType();
        let baseConvertor = new EnumerationTypeConvertor();
        baseConvertor.initFromObject(result, obj, ctxt);
        return result;
    }

    
}