import { CommonStructureConvertor } from './common-structure-convertor';
import { DataTransferObject } from '../../define/structure/data-transfer-object';
import { AggregateConvertor } from './aggregate-convertor';
import { ISerializeContext } from '../../serialize-context/extension/i-serialize-context';
import { ICommonStructureSerializeContext } from '../../serialize-context/extension/i-common-structure-serialize-context';

export class DataTransferObjectConvertor {
     convertToObject(obj:Object,dto: DataTransferObject,ctxt:ICommonStructureSerializeContext): void {
        let aggregateConvertor=new AggregateConvertor();  
        aggregateConvertor.convertToObject(obj,dto,ctxt);
    }

     initFromObject(dto:DataTransferObject,obj: Object,ctxt:ICommonStructureSerializeContext): void {
        let aggregateConvertor=new AggregateConvertor();  
        aggregateConvertor.initFromObject(dto,obj,ctxt);
    }
}