import { IPropertySerializer } from '../../extension/serializer/i-property-serializer';
import { Property } from '../../../define/property/property';
import { ISerializeContext } from '../../extension/i-serialize-context';
import { PropertyConvertor } from '../../../convertor/property/property-convertor';

export class DefaultProeprtySerializer implements IPropertySerializer {
    serialize(prop: Property, ctxt: ISerializeContext): Object {
        let result = new Object();
        let baseConvertor = new PropertyConvertor();
        baseConvertor.convertToObject(result, prop, ctxt);
        return result;
    }

    deserialize(obj: Object, ctxt: ISerializeContext): Property {
        let result = new Property();
        let baseConvertor = new PropertyConvertor();
        baseConvertor.initFromObject(result, obj, ctxt);
        return result;
    }


}