import { ISerializeContext } from '../../extension/i-serialize-context';
import { IConstraintSerializer } from '../../extension/serializer/i-constraint-serializer';
import { IConstraint } from '../../../define/constraint/i-constraint';
import { ConstraintConvertor } from '../../../convertor/constraint/constraint-convertor';

export class DefaultConstraintSerializer implements IConstraintSerializer{
    serialize(constraint: IConstraint, ctxt: ISerializeContext): Object {
        let result = new Object();
        let baseConvertor = new ConstraintConvertor();
        baseConvertor.convertToObject(result, constraint, ctxt);
        return result;
    }   
    
     deserialize(obj: Object, ctxt: ISerializeContext): IConstraint {
        throw new Error("Method not implemented.");
    }
}