import { IPropertySerializerFactory } from '../../extension/factory/i-property-serializer-factory';
import { IPropertySerializer } from '../../extension/serializer/i-property-serializer';
import { DEFAULT_PROPERTY } from '../default-constant';
import { DefaultProeprtySerializer } from '../serializer/default-property-serializer';

export class DefaultPropertySerializerFactory implements IPropertySerializerFactory {
    private serializerDict: { [key: string]: IPropertySerializer } = {};

    get(kind: string): IPropertySerializer {
        switch (kind) {
            case DEFAULT_PROPERTY:
                if (!this.serializerDict[kind]) {
                    var serializer = new DefaultProeprtySerializer();
                    this.serializerDict[kind] = serializer;
                }
                break;
            default:
                throw new Error(`${kind}!`);
        }
        return this.serializerDict[kind];
    }
}