import { Aggregate } from '../../define/structure/aggregate';
import { CommonStructureConvertor } from './common-structure-convertor';
import { ISerializeContext } from '../../serialize-context/extension/i-serialize-context';
import { ICommonStructureSerializeContext } from '../../serialize-context/extension/i-common-structure-serialize-context';

export class AggregateConvertor{

     convertToObject(obj:Object,aggregate: Aggregate,ctxt:ICommonStructureSerializeContext): void {
        let structureConvertor=new CommonStructureConvertor();  
        structureConvertor.convertToObject(obj,aggregate,ctxt);
        //root
        if(aggregate.root){
            obj['rootId']=aggregate.root.id;
        }
    }

     initFromObject(aggregate:Aggregate,obj: Object,ctxt:ICommonStructureSerializeContext): void {
        let structureConvertor=new CommonStructureConvertor();  
        structureConvertor.initFromObject(aggregate,obj,ctxt);
        //root
        let rootId=obj['rootId'];
        if(rootId&&aggregate.structuredTypes&&aggregate.structuredTypes.length>0){
            aggregate.root=aggregate.structuredTypes.find(x=>x.id==rootId);
        }
    }
}