import { CommonStructure } from '../../define/structure/common-structure';
import { StructuredType } from '../../define/data-type/structured-type/structured-type';
import { IDataType } from '../../define/data-type/i-data-type';
import { StructuredReferType } from '../../define/data-type/structured-type-refer-type/structured-refer-type';
import { EntityObject } from '../../define/structure/entity-object';
import { Operation } from '../../define/operation/operation';
import { DataType } from '../../define/data-type/data-type';

export class ReferReplaceUtil {
    static replaceOperationRefer(allStructureTypes: StructuredType[], operations: Operation[]) {
        if (operations != null) {
            operations.forEach(op => {
                let retType = op.returnType;
                if (retType != null && retType instanceof StructuredReferType) {
                    let structureTypeId: string = (retType as StructuredReferType).refId;
                    let queryStructureType = this.getRefStructureType(allStructureTypes, structureTypeId);
                    op.returnType = queryStructureType;
                }
                if (op.parameters != null) {
                    op.parameters.forEach(parameter => {
                        let parameterType = parameter.parameterType;
                        if (parameterType instanceof StructuredReferType) {
                            let structureTypeId: string = (parameterType as StructuredReferType).refId;
                            let queryStructureType = this.getRefStructureType(allStructureTypes, structureTypeId);
                            parameter.parameterType = queryStructureType;
                        }
                    })
                }
            })
        }
    }


    static replacePropertyRefer(structure: CommonStructure) {
        let allStructures = this.getAllStructuredTypes(structure);
        if (structure.structuredTypes != null) {
            structure.structuredTypes.forEach(structureType => {
                if (structureType.properties != null) {
                    structureType.properties.forEach(prop => {
                        if (prop.propertyType instanceof StructuredReferType) {
                            let structureTypeId = (prop.propertyType as StructuredReferType).refId;
                            let queryStructureType = this.getRefStructureType(allStructures, structureTypeId);
                            prop.propertyType = queryStructureType;
                        }
                    })
                }
            })
        }
        if (structure.refStructures != null) {
            structure.refStructures.forEach(refStructure => {
                this.replacePropertyRefer(refStructure);
            })
        }
    }

    static getRefStructureType(allStructures: StructuredType[], structureId: string): StructuredType {
        let queryStructureType = allStructures.find(x => x.id == structureId);
        return queryStructureType;
    }

    static getAllStructuredTypes(structure: CommonStructure) {
        let array: Array<StructuredType> = new Array<StructuredType>();
        if (structure.structuredTypes != null) {
            array=array.concat(structure.structuredTypes);
        }
        if (structure.refStructures != null) {
            structure.refStructures.forEach(refStructure => {
                if (refStructure.structuredTypes != null) {
                    array=array.concat(refStructure.structuredTypes);
                }
            })
        }
        return array;
    }



}