import { RelationConvertor } from './relation-convertor';
import { EntityRelation } from '../../define/structure/entity-relation';

export class RelationDictConvertor {

    convertorToObject(relationDict: { string: { string: Array<EntityRelation> } }): Object {
        return relationDict;
    }

    initFromObject(obj: Object): { string: { string: Array<EntityRelation> } } {
        let relationDict = new Object();
        if (!obj) {
            return relationDict as { string: { string: Array<EntityRelation> } };
        }
        let pCodeList = Object.keys(obj);
        pCodeList.forEach(pCode => {
            let cDict = obj[pCode];
            if (cDict) {
                relationDict[pCode] = new Object();
                let cCodeList = Object.keys(cDict);
                cCodeList.forEach(cCode => {
                    let cValue = cDict[cCode];
                    if (cValue) {
                        relationDict[pCode][cCode] = new Array<EntityRelation>();
                        cValue.forEach(relationObj => {
                            let relationConvertor = new RelationConvertor();
                            let relation = relationConvertor.initFromObject(relationObj);
                            relationDict[pCode][cCode].push(relation);
                        })
                    }
                })
            }
        })
        return relationDict as { string: { string: Array<EntityRelation> } };
    }
}