import { CommonStructureConvertor } from './common-structure-convertor';
import { Operation } from '../../define/operation/operation';
import { OperationConvertor } from '../operation/operation-convertor';
import { ReferReplaceUtil } from '../utils/refer-replace-util';
import { EntityObject } from '../../define/structure/entity-object';
import { AggregateConvertor } from './aggregate-convertor';
import { StructuredType } from '../../define/data-type/structured-type/structured-type';
import { ISerializeContext } from '../../serialize-context/extension/i-serialize-context';
import { ICommonStructureSerializeContext } from '../../serialize-context/extension/i-common-structure-serialize-context';

export class EntityObjectConvertor  {

     convertToObject(obj:Object,entity: EntityObject,ctxt:ICommonStructureSerializeContext): void {
        let aggregateConvertor=new AggregateConvertor();  
        aggregateConvertor.convertToObject(obj,entity,ctxt);
        obj['entityRelations'] = entity.entityRelations;
        obj['operations'] = new Array<Object>();
        if (entity.operations) {
            entity.operations.forEach(op => {
                var kind=op['kind'];
                var serializer=ctxt.getOperationSerializerFactory().get(kind);
                var opObj=serializer.serialize(op,ctxt);
                obj['operations'].push(opObj);
            });
        }
    }

     initFromObject(entity:EntityObject,obj: Object,ctxt:ICommonStructureSerializeContext): void {
        let aggregateConvertor=new AggregateConvertor();  
        aggregateConvertor.initFromObject(entity,obj,ctxt);
        if (obj['entityRelations']) {
            entity.entityRelations = obj['entityRelations'];
        }
        if (obj['operations']) {
            entity.operations = new Array<Operation>();
            obj['operations'].forEach(operationObj => {
                var kind=operationObj['kind'];
                var serializer=ctxt.getOperationSerializerFactory().get(kind);
                var operation=serializer.deserialize(operationObj,ctxt);
                entity.operations.push(operation);
            });
        }
        let allStructureTypes:Array<StructuredType>=ReferReplaceUtil.getAllStructuredTypes(entity);
        ReferReplaceUtil.replaceOperationRefer(allStructureTypes,entity.operations);
    }
}