import { Parameter } from '../parameter/parameter';
import { IDataType } from '../data-type/i-data-type';
import { ElementConstant } from '../constant/element-const';
import { Extension } from '../../extension';

export class Operation extends Extension {
    code: string;
    name: string;
    description: string;
    uri: string;
    parameters: Array<Parameter>;
    returnType:IDataType;
    kind:string;

    constructor(){
        super();
        this.kind=ElementConstant.DEFAULT_OPERATION;
    }
}