import { IDataTypeSerializer } from '../../extension/serializer/i-data-type-serializer';
import { IDataType } from '../../../define/data-type/i-data-type';
import { ISerializeContext } from '../../extension/i-serialize-context';
import { StructuredTypeConvertor } from '../../../convertor/data-type/structured-type-convertor';
import { StructuredType } from '../../../define/data-type/structured-type/structured-type';

export class DefaultStructuredTypeSerializer implements IDataTypeSerializer{
    serialize(type: IDataType, ctxt: ISerializeContext): Object {
        let result=new Object();
        let baseConvertor=new StructuredTypeConvertor();
        baseConvertor.convertToObject(result,type,ctxt);
        return result;
    }    
    
    deserialize(obj: Object, ctxt: ISerializeContext): IDataType {
        let result = new StructuredType();
        let baseConvertor = new StructuredTypeConvertor();
        baseConvertor.initFromObject(result, obj, ctxt);
        return result;
    }

    
}