import { IConstraint } from '../../define/constraint/i-constraint';
import { ConstraintConst } from '../../define/constant/constraint-const';
import { DecimalConstraint } from '../../define/constraint/decimal-constraint';
import { StringConstraint } from '../../define/constraint/string-constraint';
import { ISerializeContext } from '../../serialize-context/extension/i-serialize-context';


export class ConstraintConvertor {

    convertToObject(obj: Object, constraint: IConstraint, ctxt: ISerializeContext): void {
        if (!constraint) {
            return;
        }
        switch (constraint.getKind()) {
            case ConstraintConst.DECIMAL:
                let decimalconstraint = constraint as DecimalConstraint;
                obj['scale'] = <number>decimalconstraint.scale;
                obj['pricision'] = <number>decimalconstraint.pricision;
            case ConstraintConst.STRING:
                let stringconstraint = constraint as StringConstraint;
                obj['maxLength'] = <number>stringconstraint.maxLength;
            default:
                throw new Error(`${constraint.getKind()}`);
        }
    }

    initFromObject(constraint: IConstraint, obj: Object, ctxt: ISerializeContext): void {
        if (!obj) {
            return null;
        }
        switch (obj['kind']) {
            case ConstraintConst.DECIMAL:
            case ConstraintConst.STRING:
            default:
                throw new Error(`${obj['kind']}`);
        }
    }
}