import { ICommonStructureSerializer } from '../../extension/serializer/i-common-structure-serializer';
import { CommonStructure } from '../../../define/structure/common-structure';
import { ISerializeContext } from '../../extension/i-serialize-context';
import { EntityObjectConvertor } from '../../../convertor/structure/entity-object-convertor';
import { EntityObject } from '../../../define/structure/entity-object';
import { ICommonStructureSerializeContext } from '../../extension/i-common-structure-serialize-context';

export class DefaultEntityObjectSerializer implements ICommonStructureSerializer {
    serialize(structure: CommonStructure, ctxt: ICommonStructureSerializeContext): Object {
        let result = new Object();
        //
        let baseConvertor = new EntityObjectConvertor();
        let entity = structure as EntityObject;
        baseConvertor.convertToObject(result, entity, ctxt);
        //Cando:Object
        return result;
    }


    deserialize(obj: Object, ctxt: ICommonStructureSerializeContext): CommonStructure {
        let result = new EntityObject();
        let baseConvertor = new EntityObjectConvertor();
        baseConvertor.initFromObject(result, obj, ctxt);
        return result;
    }

}