import { PrimitiveTypeKind } from './primitive-type-kind';
import { IDataType } from '../i-data-type';
import { DataType } from '../data-type';
import { DataTypeConst } from '../../constant/data-type-const';

/**
 * 
 */
export class PrimitiveType extends DataType implements IDataType {

    constructor(){
        super();
        this.kind=DataTypeConst.DEFAULT_PRIMITIVE;
    }

    /**
     * Int,Bool,Float,Double
     */
    primitiveTypeKind: PrimitiveTypeKind;
}